/**
 * Workspace configuration management
 *
 * Handles saving, loading, and clearing workspace configuration.
 */

import { mkdir, readFile, unlink, writeFile, chmod } from "node:fs/promises";
import { dirname } from "node:path";
import { logger } from "../utils/logger.js";

// Config file path - should match what's in constants
const CONFIG_FILE = (() => {
  const { homedir } = require("node:os");
  const { join } = require("node:path");
  const CLAUDE_ZEST_DIR = join(
    homedir(),
    `.claude-zest${process.env.NODE_ENV === "development" ? "-dev" : ""}`,
  );
  return join(CLAUDE_ZEST_DIR, "config.json");
})();

export interface WorkspaceConfig {
  workspace_id: string;
  workspace_name: string;
}

/**
 * Save workspace configuration to disk
 */
export async function saveWorkspaceConfig(config: WorkspaceConfig): Promise<void> {
  try {
    await mkdir(dirname(CONFIG_FILE), { recursive: true });
    await writeFile(CONFIG_FILE, JSON.stringify(config, null, 2), "utf-8");
    await chmod(CONFIG_FILE, 0o600); // Read/write for owner only
    logger.info("Workspace config saved", {
      workspace_id: config.workspace_id,
      workspace_name: config.workspace_name,
    });
  } catch (error) {
    logger.error("Failed to save workspace config", error);
    throw new Error("Failed to save workspace configuration");
  }
}

/**
 * Load workspace configuration from disk
 */
export async function loadWorkspaceConfig(): Promise<WorkspaceConfig | null> {
  try {
    try {
      const content = await readFile(CONFIG_FILE, "utf-8");
      const config = JSON.parse(content) as WorkspaceConfig;
      logger.debug("Workspace config loaded", {
        workspace_id: config.workspace_id,
        workspace_name: config.workspace_name,
      });
      return config;
    } catch (error) {
      if (error instanceof Error && "code" in error && (error as NodeJS.ErrnoException).code === "ENOENT") {
        logger.debug("No workspace config found");
        return null;
      }
      throw error;
    }
  } catch (error) {
    logger.error("Failed to load workspace config", error);
    return null;
  }
}

/**
 * Clear workspace configuration from disk
 */
export async function clearWorkspaceConfig(): Promise<void> {
  try {
    await unlink(CONFIG_FILE);
    logger.info("Workspace config cleared");
  } catch (error) {
    if (error instanceof Error && "code" in error && (error as NodeJS.ErrnoException).code === "ENOENT") {
      return; // File doesn't exist, nothing to clear
    }
    logger.error("Failed to clear workspace config", error);
    throw new Error("Failed to clear workspace configuration");
  }
}
